@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body{margin:0;padding:0;height:100vh;font-family:'Varela Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

#landing-page{height:3000px;background:#d9a7c7;background:linear-gradient(to top, #fffcdc, #d9a7c7);overflow:hidden}#landing-page .particle-background{position:absolute;margin-top:-10px;width:100vw;height:150vh}@media screen and (max-width: 910px){.particle-background{height:100vh}}

#nav-container{display:flex;flex-direction:row;justify-content:space-between}#nav-container #logo-left{height:160px;display:flex;align-items:flex-end;width:20vw;margin-left:75px}#nav-container #logo-left a{margin:0;padding:0;text-transform:none;text-decoration:none}#nav-container #logo-left a img{margin:0;padding:0;width:50px;height:auto;padding:10px;border:2px solid white;border-radius:50%;margin-bottom:10px}#nav-container #logo-left h2{margin:0;padding:0;font-size:2rem;font-weight:400}#nav-container #logo-left h2 span{color:red}#nav-container #rightside-container{height:160px;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end}#nav-container #rightside-container .profile-container{display:flex;flex-direction:row}#nav-container #rightside-container .profile-container h4{margin:0;padding:0;margin-right:25px;margin-top:3px;letter-spacing:2px;color:white}#nav-container #rightside-container .profile-container .profile-bar{margin:0;padding:0;width:350px;height:25px;margin-bottom:25px;margin-right:75px;border-radius:15px;background:#6fade3;background:linear-gradient(90deg, rgba(111,173,227,0.500619) 0%, #6fade3 70%, #fff 70%)}#nav-container #rightside-container .navlink-container{margin:0;padding:0;margin-bottom:10px;margin-right:75px;width:80vw;display:flex;flex-direction:row;justify-content:flex-end}#nav-container #rightside-container .navlink-container a{position:relative;font-size:1rem;font-weight:500;color:white;text-decoration:none;cursor:pointer;z-index:0;padding:5px 20px;border:1.5px solid white;border-radius:30px;margin-left:52.5px}#nav-container #rightside-container .navlink-container a:before,#nav-container #rightside-container .navlink-container a:after{position:absolute;left:50%;top:50%;width:40px;height:40px;border:4px solid #FFF9E3;transform:translateX(-50%) translateY(-50%) scale(0.8);border-radius:50%;background:transparent;content:"";opacity:0;transition:all 0.3s;z-index:-1}#nav-container #rightside-container .navlink-container a:after{border-width:2px;transition:all 0.4s}#nav-container #rightside-container .navlink-container a:hover:before{opacity:0.7;transform:translateX(-50%) translateY(-50%) scale(1)}#nav-container #rightside-container .navlink-container a:hover:after{opacity:0.7;transform:translateX(-50%) translateY(-50%) scale(1.3)}@media screen and (max-width: 910px){#nav-container{display:grid}#nav-container #logo-left{margin:0;padding:0;height:75px;width:100vw;display:grid;place-items:center;background-color:black}#nav-container #logo-left a img{margin:0;padding:0;width:25px;height:auto;padding:10px;border:2px solid white;border-radius:50%}#nav-container #rightside-container{display:none}#nav-container{height:100px}}

#infocarousel-container #ic-description-container{width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container #ic-description-container h1{margin:0;padding:0;margin-left:1vw;margin-top:50px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#infocarousel-container .info-carousel{margin-bottom:25px;width:80vw;height:40vw;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#infocarousel-container .info-carousel img{height:100vh;width:auto}#infocarousel-container .description{display:none}.swiper-button-prev{color:white;left:0;padding-left:25px}.swiper-button-next{color:white;right:0;padding-right:25px}@media screen and (max-width: 910px){#infocarousel-container #ic-description-container{width:90vw}#infocarousel-container #ic-description-container h1{font-size:1.5rem}#infocarousel-container .info-carousel{width:90vw;height:60vw}#infocarousel-container .info-carousel img{width:auto;height:100%}#infocarousel-container .description{display:block;width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container .description h1{font-family:"Montserrat", 'sans-serif';font-size:1.75rem;font-weight:700}#infocarousel-container .description p{font-family:"Montserrat", 'sans-serif';font-size:0.85rem;letter-spacing:1px;margin:0;padding:0;font-weight:300}.swiper-button-next,.swiper-button-prev{display:none}}

#dashboard-title{margin:0;padding:0;margin-left:10vw;margin-top:100px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#dashboard-container{left:0;right:0;margin:auto;width:80vw;height:auto;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-between;align-items:flex-start}#dashboard-container .Gifts{width:42.5vw;height:35vw;display:flex;flex-direction:column;justify-content:space-between}#dashboard-container .Gifts .top-user{width:100%;height:35%;display:grid;place-items:center;background-color:rgba(255,255,255,0.25);border-radius:30px}#dashboard-container .Gifts .top-user div{width:90%;height:70%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}#dashboard-container .Gifts .top-user div h1{font-size:3rem;padding:10px 20px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center;align-items:center}#dashboard-container .Gifts .top-user div .description p{margin:0;padding:0;width:90%}#dashboard-container .Gifts .top-user div .description div{width:90%;height:17.5px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{width:100%;height:60%;display:grid;place-items:center;background-color:rgba(255,255,255,0.25);border-radius:30px}@media screen and (max-width: 910px){#dashboard-title{font-size:1.5rem;margin-left:6vw}#dashboard-container{width:90vw}#dashboard-container .Gifts{width:100%;height:auto}#dashboard-container .Gifts .top-user{height:35vw;margin-top:30px}#dashboard-container .Gifts .top-user div h1{font-size:2rem;padding:11.5px 17.5px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description p{font-size:0.65rem}#dashboard-container .Gifts .top-user div .description div{width:90%;height:12px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{height:50vw;margin-top:30px}}

#chart{display:grid;place-items:center;margin:0;padding:0;width:35vw;height:35vw;background-color:#f8f8f8;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#chart .chart-title-container{width:100%;display:grid;place-items:center}#chart .chart-title-container h3{margin:15px 0 0 0;padding:0;color:#4d4d4d;font-size:1.15rem;font-family:'Varela Round', sans-serif}#chart .chart-button-container{width:100%;height:auto;display:flex;flex-direction:row;justify-content:space-evenly;margin-bottom:10px}#chart .chart-button-container .see_chart_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;margin:0;padding:10px 10px;border:1px solid black;border-radius:10px;background:white;cursor:pointer}#chart .chart-button-container .see_data_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;color:white;margin:0;padding:10px 47.5px;border:none;border-radius:10px;background:#333;cursor:pointer}@media screen and (max-width: 910px){#chart{width:100%;min-height:400px}#chart .chart-title-container h3{font-size:1rem}#chart .chart-button-container .see_chart_button{font-size:1rem;border:1px solid black}#chart .chart-button-container .see_data_button{font-size:1rem;padding:10px 47.5px}}@media screen and (max-width: 410px){#chart{min-height:375px}#chart .chart-title-container h3{font-size:0.75rem}#chart .chart-button-container{margin-bottom:7.5px}}

